.item-select__wrapper.is-active span:last-child {
  transform: rotate(-180deg);
  opacity: 1;
  color: #ffffff;
}
@media (hover: hover) {
  .item-select__wrapper.is-active:hover:not(.focus-visible) span:last-child {
    transform: rotate(0deg);
    opacity: 1;
    color: #ffffff;
  }
}
.item-select__wrapper.is-active.focus-visible:focus span:last-child {
  transform: rotate(0deg);
  opacity: 1;
  color: #ffffff;
}

.dropdown-modal {
  position: relative;
}

.dropdown-modal__btn {
  mask-image: url("../img/icons/select-arrow.svg");
  width: 16px;
  height: 16px;
  mask-size: contain;
  background-color: #484848;
  mask-repeat: no-repeat;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  background-size: contain;
  background-position: center center;
  transition: transform 0.3s ease;
}
.dropdown-modal__btn.focus-visible:focus {
  transform: rotate(-180deg);
}

.dropdown-modal__list {
  opacity: 0;
  position: absolute;
  right: 0;
  top: 53px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  overflow: hidden;
  border-radius: 8px;
  width: 100%;
  z-index: 1;
  border: 1px solid rgba(72, 72, 72, 0.3);
  transition: opacity 0.3s ease, visibility 0.3s ease;
  visibility: hidden;
}
.dropdown-modal__list--visible {
  opacity: 1;
  visibility: visible;
}

.dropdown-modal__item {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #000000;
  margin: 0;
  padding: 17px 20px;
  text-transform: capitalize;
  cursor: pointer;
}
.dropdown-modal__item:last-child {
  border-bottom: none;
}
.dropdown-modal__item--active {
  color: #ffffff;
  background-color: #316dc2;
}

.dropdown-modal__text-wrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px 16px 25px;
}
@media (max-width: 1199px) {
  .dropdown-modal__text-wrap {
    padding-right: 20px;
  }
}

.dropdown-modal__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: capitalize;
}

